module.exports = "/* The switch - the box around the slider */\r\n.switch {\r\n    position: relative;\r\n    display: inline-block;\r\n    width: 50px;\r\n    height: 25px;\r\n  }\r\n.switch+span{position: relative; top: 4px; padding-left: 10px;}\r\n/* Hide default HTML checkbox */\r\n.switch input {\r\n    opacity: 0;\r\n    width: 0;\r\n    height: 0;\r\n  }\r\n/* The slider */\r\n.slider {\r\n    position: absolute;\r\n    cursor: pointer;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background-color: #ccc;\r\n    transition: .4s;\r\n  }\r\n.slider:before {\r\n    position: absolute;\r\n    content: \"\";\r\n    height: 17px;\r\n    width: 17px;\r\n    left: 4px;\r\n    bottom: 4px;\r\n    background-color: white;\r\n    transition: .4s;\r\n  }\r\ninput:checked + .slider {\r\n    background-color: #f0713c;\r\n  }\r\ninput:focus + .slider {\r\n    box-shadow: 0 0 1px #f0713c;\r\n  }\r\ninput:checked + .slider:before {\r\n    -webkit-transform: translateX(26px);\r\n    transform: translateX(26px);\r\n  }\r\n/* Rounded sliders */\r\n.slider.round {\r\n    border-radius: 34px;\r\n  }\r\n.slider.round:before {\r\n    border-radius: 50%;\r\n  }\r\nlabel.custom-file-upload {\r\n    border: 1px solid #ccc;\r\n    width: 120px;\r\n    height: 120px;\r\n    box-shadow: 0 0 1px #ccc;\r\n    vertical-align: middle;\r\n    display: table-cell;\r\n    text-align: center;\r\n    cursor: pointer;\r\n    position: relative;\r\n    color:#f0713c;\r\n    font-weight: bold;\r\n    background: #f4f4f4;\r\n}\r\n.input-file-trigger input{visibility: hidden;position: absolute;}\r\n.custom-file-upload i { font-size: 45px; }\r\nlabel.custom-file-upload img{width: 100%; height: auto; max-height: 100%;}\r\n.ticketimg{ width: 62px;}\r\n.ticketNo{color:#374149}\r\n.input-file-trigger {\r\n    padding: 10px 35px;\r\n    background: #f0713c;\r\n    color: #fff;\r\n    font-size: 1em;\r\n    transition: all .4s;\r\n    cursor: pointer;\r\n    max-width: 200px;\r\n  }\r\n.img-cross-btn{\r\n    position: absolute;\r\n    right: 10px;\r\n    background: #fff;\r\n    border: 1px solid #ccc;\r\n    padding: 0 4px;\r\n    border-radius: 50%;\r\n    top: -4px;\r\n    font-size: 10px;\r\n  }\r\n.btn-size{color:#fff !important}"