  // paged items
  pagedItems: any[];
  technicians: any;
  requestId: any;
  technicianKey: any;
  selectedFaseId: any="";
  statusId:number=2;
  stepNumber: number;
  isShowLoader: boolean=false;
  isStatusFilter:boolean=true;
  filterObj={step:this.stepNumber,statusId:2,subjectId:'',startDate:'',endDate:'',email:'',searchkey:''};
