import { Injectable } from '@angular/core';
import * as _ from 'underscore';
import { environment } from '../../environments/environment';
import { Http, Response, Request, RequestMethod } from "@angular/http";
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { NgFlashMessageService } from 'ng-flash-messages';
@Injectable({
  providedIn: 'root'
})
export class CommonService {
    auth_token: string;

  constructor(private httpClient: HttpClient,private flashService:NgFlashMessageService,) { }

  getPager(totalItems: number, currentPage: number = 1, pageSize: number = 10) {
    // calculate total pages
    let totalPages = Math.ceil(totalItems / pageSize);

    let startPage: number, endPage: number;
    // if (totalPages <= 10) {
    //     // less than 10 total pages so show all
    //     startPage = 1;
    //     endPage = totalPages;
    // } else {
    //     // more than 10 total pages so calculate start and end pages
    //     if (currentPage <= 6) {
    //         startPage = 1;
    //         endPage = 10;
    //     } else if (currentPage + 4 >= totalPages) {
    //         startPage = totalPages - 9;
    //         endPage = totalPages;
    //     } else {
    //         startPage = currentPage - 5;
    //         endPage = currentPage + 4;
    //     }
    // }
    
    if (totalPages <= 5) {
        startPage = 1;
        endPage = totalPages;
    } else {
        if (currentPage <= 3) {
            startPage = 1;
            endPage = 5;
        } else if (currentPage + 1 >= totalPages) {
            startPage = totalPages - 4;
            endPage = totalPages;
        } else {
            startPage = currentPage - 2;
            endPage = currentPage+2;
        }
    }

    // calculate start and end item indexes
    let startIndex = (currentPage - 1) * pageSize;
    let endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

    // create an array of pages to ng-repeat in the pager control
    let pages = _.range(startPage, endPage + 1);

    // return object with all pager properties required by the view
    return {
        totalItems: totalItems,
        currentPage: currentPage,
        pageSize: pageSize,
        totalPages: totalPages,
        startPage: startPage,
        endPage: endPage,
        startIndex: startIndex,
        endIndex: endIndex,
        pages: pages
    };
}

sendMail(requestData)
{
    return this.httpClient.post<any>(`${environment.apiUrl}/step/sendMail?token=`+this.getAuthToken(), requestData).pipe(
        map(
          Response => {
            if (Response.status != 0) {
              return  Response ;
            } else {
              status = Response.message;
              alert(status);
            }
          },
          error => {
            let msg = error;
          }
        )
      );
}
getAuthToken():string
  {
    const currentUsertoken = localStorage.getItem("token");
    if (currentUsertoken) {
      this.auth_token = currentUsertoken;
    }
    return currentUsertoken;
  }
  showMessage(message,type='success')
  {
    this.flashService.showFlashMessage({
      // Array of messages each will be displayed in new line
      messages: [message], 
      // Whether the flash can be dismissed by the user defaults to false
      dismissible: true, 
      // Time after which the flash disappears defaults to 2000ms
      timeout: 2000,
      // Type of flash message, it defaults to info and success, warning, danger types can also be used
      type: type,
    });
  }
}
