import { CommonService } from 'src/app/services/common.service';
import { Notification } from "./../../models/notification";
import { Step, Quote, Technician } from "./../../models/step";
import { Component, OnInit } from "@angular/core";
import { AmazingTimePickerService } from "amazing-time-picker";
import { UserModel } from "src/app/models/user-model";
import { Office } from "src/app/models/office";
import { ApiService } from "src/app/services/api.service";
import { Request } from "src/app/models/request";
import { Router, ActivatedRoute } from "@angular/router";
import { Tower } from "src/app/models/tower";
import { List } from "linqts";
import { NgbCalendar } from "@ng-bootstrap/ng-bootstrap";
import { NgbDateParserFormatter } from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: "app-maintenance-edit",
  templateUrl: "./maintenance-edit.component.html",
  styleUrls: ["./maintenance-edit.component.css"]
})
export class MaintenanceEditComponent implements OnInit {
  show = false;
  selectedTime: any;
  officeId: any;
  selectedTowerId: string = "";
  selectedFloorId: string = "";
  officeDetail: Office = new Office();
  userModel: UserModel = new UserModel();
  technicianModel: UserModel = new UserModel();
  stepModel: Step = new Step();
  requestModel: Request = new Request();
  subjects: any = [];
  requestId: any;
  floors: any;
  towers: any;
  stepId: any;
  selectedTime1: string;
  techDate: any = "";
  quoteDate: any = "";
  isStep1Disabled: boolean = false;
  isStep2Disabled: boolean = false;
  isStep3Disabled: boolean = false;
  isStep4Disabled: boolean = false;
  step1Tab: Boolean = true;
  step2Tab: Boolean;
  step3Tab: Boolean;
  step4Tab: Boolean;
  step5Tab: Boolean;
  images: any = [];
  notificationModel: Notification = new Notification();
  technicianKey: any;
  stepRating: number;
  largeImageUrl: any;
  showTechModal: boolean;
  technicians: any;
  constructor(
    private parserFormatter: NgbDateParserFormatter,
    private calendar: NgbCalendar,
    private atp: AmazingTimePickerService,
    private apiservice: ApiService,
    private route: ActivatedRoute,
    private commonService: CommonService,
    private router: Router
  ) { }

  ngOnInit() {
    this.getLocalStorage();
    // this.quoteDate = this.calendar.getToday();
    this.route.params.subscribe(params => {
      this.requestId = params['id'];
      this.getRequestById();
      this.getStepByReqId();
      this.getAllTechnician() ;

    });

    this.getSubjects();
  }

  
  // selectToday() {
  //   this.techDate = this.calendar.getToday();
  //   // this.stepModel.technician_info.date = this.calendar.getToday();
  // }
  open() {

    const amazingTimePicker = this.atp.open();
    amazingTimePicker.afterClose().subscribe(time => {
      this.selectedTime = time;
      this.stepModel.quote.time = time;
    });
  }
  techOpen() {

    const amazingTimePicker = this.atp.open();
    amazingTimePicker.afterClose().subscribe(time => {
      this.selectedTime1 = time;
      this.stepModel.technician_info.time = time;
    });
  }
  getStepByReqId() {
    if (this.requestId)
      this.apiservice.getStepByReqId(this.requestId).subscribe(data => {
        if (data) {
          this.stepModel = data;
          this.stepModel.quote = data.quote ? data.quote : new Quote();

          let savedDate = new Date(this.stepModel.quote.date);
          this.quoteDate = { year: savedDate.getFullYear(), month: savedDate.getMonth() + 1, day: savedDate.getDate() }
          this.stepModel.technician_info = data.technician_info ? data.technician_info : new Technician();

          let savedTechDate = new Date(this.stepModel.technician_info.date);
          this.techDate = { year: savedTechDate.getFullYear(), month: savedTechDate.getMonth() + 1, day: savedTechDate.getDate() }
          //this.quoteDate=
          this.stepId = data._id;
          this.stepRating = parseInt(this.stepModel.star);
          this.activate("t" + this.stepModel.step);
          //          console.log("step", this.stepModel);
        }
      });
  }
  getRequestById() {
    if (this.requestId)
      this.apiservice.getRequestById(this.requestId).subscribe(data => {
        if (data && data.length > 0) {
          this.requestModel = data[0];
          if (!this.requestModel.technicianKey || this.requestModel.technicianKey == 'undefined') {
            //alert("Please assign technician first");
            alert("Por favor, asigne un técnico primero");
            return;
          }
          this.technicianKey = this.requestModel.technicianKey;
          this.getUserById();
          //    this.stepId = data[0].stepId ? data[0].stepId._id : "";
          if (this.requestModel.techPhoto.length > 0) {
            this.images = this.requestModel.techPhoto;
          }
          //this.images=["https://www.gstatic.com/webp/gallery/4.sm.jpg","https://www.gstatic.com/webp/gallery/3.sm.jpg","https://www.gstatic.com/webp/gallery/2.sm.jpg"];
          //console.log("request", this.requestModel);
        }
      });
  }
  getUserById() {
    if (this.technicianKey)
      this.apiservice.getUserById(this.technicianKey).subscribe(data => {
        if (data && data.length > 0) {
          this.technicianModel = data[0];
          this.stepModel.quote.name = this.technicianModel.first_name + ' ' + this.technicianModel.last_name;
          this.stepModel.quote.phone = this.technicianModel.phone_number
          this.stepModel.quote.company = this.technicianModel.company;

          this.stepModel.technician_info.name = this.technicianModel.first_name + ' ' + this.technicianModel.last_name;
          this.stepModel.technician_info.phone = this.technicianModel.phone_number
          this.stepModel.technician_info.company = this.technicianModel.company;
          //          this.stepModel.technician_info= this.stepModel.quote;
        }
      });
  }
  createNotification(email?, notfMsg?) {
    this.apiservice.createNotification(this.notificationModel).subscribe(data => {
      //console.log(data);
      let msg = data;
      //this.router.navigateByUrl('/Maintenance');
      //this.subjects = data;
      //console.log("office",this.officeDetail);
    });
  }
  updateRequest(step, statusID) {
    if (this.requestId) {
      let requestData = { step: step, statusId: statusID };
      this.apiservice.updateRequestDetail(this.requestId, requestData).subscribe(data => {
        if (data.status != 0) {
          //this.requestModel = data[0];
          console.log("request", this.requestModel);
        }
      });
    }
  }

  goToStep1_5() {

    if (!isNaN(this.quoteDate) || !this.stepModel.quote.time) {
      alert("Por favor ingrese fecha u hora");
      return;
    }
    var ts = new Date();
    this.stepModel.step = 1.5;
    this.requestModel.statusId = 2;
    this.stepModel.updated_at1 = ts.toJSON();

    let date = this.parserFormatter.format(this.quoteDate);
    let dateTime = new Date(date + ' ' + this.stepModel.quote.time);
    this.stepModel.quote.date = dateTime.toJSON();

//    console.log("stepModel", this.stepModel);
    //this.activate("t2");
    if (this.stepId)
      this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
        if (data.status != 0) {
          this.activate("t2");
          this.notificationModel.email = this.requestModel.email;
          this.notificationModel.notification = "EG ha programado una visita inicial para hacerle una cotización.";
          this.notificationModel.requestId = this.requestId;

          this.createNotification();
          this.isStep1Disabled = this.stepModel.step >= 1.5 ? true : false;
          //Technician notification
          // this.notificationModel.email = this.technicianModel.email;
          // this.createNotification();

          this.updateRequest(this.stepModel.step, 2);
        }
      });
  }

  goToStep2() {
    //Cotizaction   
    this.stepModel.step = 2;
    this.requestModel.statusId = 2;
    this.stepModel.sendQuotation_at = new Date().toJSON();
    //this.activate("t3");
    this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
      if (data.status != 0) {
        this.isStep2Disabled = this.stepModel.step >= 2 ? true : false;
        this.activate("t3");
        this.notificationModel.email = this.requestModel.email;
        this.notificationModel.notification = "EG le ha enviado una cotización.";
        this.notificationModel.requestId = this.requestId;
        this.createNotification();
        this.updateRequest(this.stepModel.step, this.requestModel.statusId);
        //Send Quotation
        let mailData = {
          sender: "mobilytesolu123@gmail.com",
          reciever:
          //"pawan.kumar@mobilyte.com"
            //"nav@mailinator.com"
            this.requestModel.email
        }
        this.sendEMail(mailData);
      }
    });
  }

  sendEMail(mailData) {
    this.apiservice.sendMail(mailData).subscribe(data => {
      console.log(data);
    });
  }
  goToStep3() {

    if (!isNaN(this.techDate) || !this.stepModel.technician_info.time) {
      alert("Por favor ingrese fecha u hora");
      return;
    }
    //technica info
    this.stepModel.step = 3;
    this.requestModel.statusId = 2;
    //this.stepModel.updated_at1 = new Date().toLocaleDateString();
    //this.stepModel.technician_info.date = "2019-02-04";
    let date = this.parserFormatter.format(this.techDate);
    let dateTime = new Date(date + ' ' + this.stepModel.technician_info.time);
    //  this.stepModel.quote.date =dateTime.toJSON();
    this.stepModel.technician_info.date = dateTime.toJSON();
    // this.stepModel.updated_at3 = Date.now().toString();

    if (this.stepId)
      this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
        if (data.status != 0) {
          this.isStep3Disabled = this.stepModel.step >= 3 ? true : false;
          this.updateRequest(this.stepModel.step, this.requestModel.statusId);
          this.notificationModel.email = this.requestModel.email;
          this.notificationModel.notification = "EG programó la visita del técnico para el día y hora a continuación: " + date + " " + this.stepModel.technician_info.time;
          this.notificationModel.requestId = this.requestId;
          this.createNotification();
          // this.notificationModel.email = this.technicianModel.email;
          // this.createNotification();
          this.activate("t3");

        }
      });
  }
  goToStep4() {
    this.stepModel.step = 4;
    this.stepModel.is_completed = true;
    // this.requestModel.status = "3";
    this.stepModel.updated_at3 = new Date().toJSON();
    if (this.stepId)
      this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
        if (data.status != 0) {
          this.isStep3Disabled = this.stepModel.step >= 3 ? true : false;
          this.updateRequest(this.stepModel.step, this.requestModel.statusId);
          this.notificationModel.email = this.requestModel.email;
          this.notificationModel.notification = "EG ha finalizado los trabajos. Su factura está disponible para pago.";
          this.notificationModel.requestId = this.requestId;
          this.createNotification();
          this.activate("t4");
        }
      });
  }
  paidInvoice() {
    this.stepModel.step = 5;
    //this.stepModel.is_completed = true;
    this.stepModel.is_paid = true;
    this.stepModel.updated_at4 = new Date();
    if (this.stepId)
      this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
        if (data.status != 0) {
          this.isStep3Disabled = this.stepModel.step >= 3 ? true : false;
          this.updateRequest(this.stepModel.step, this.requestModel.statusId);
          this.notificationModel.email = this.requestModel.email;
          this.notificationModel.notification = "EG recibió su pago. Por favor califique el servicio.";
          this.notificationModel.requestId = this.requestId;
          this.createNotification();

          this.activate("t5");
          //alert("Updated Step 3");
        }
      });
  }
  archieveTicket() {
    this.stepModel.step = 6;
    this.requestModel.statusId = 3;
    this.stepModel.is_completed = true;
    this.stepModel.updated_at6 = new Date().toJSON();
    if (this.stepId)
      this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
        if (data.status != 0) {
          // this.isStep3Disabled = this.stepModel.step >= 3 ? true : false;
          this.updateRequest(this.stepModel.step, this.requestModel.statusId);
          this.notificationModel.email = this.requestModel.email;
          this.notificationModel.notification = "EG ha archivado su solicitud";
          this.notificationModel.requestId = this.requestId;
          this.createNotification();
          this.notificationModel.email = this.technicianModel.email;
          this.createNotification();
          this.router.navigate(['/Incio']);
          //this.router.navigateByUrl('/Incio');
          //this.activate("t5");
          //  alert("EG ha archivado su solicitud");
        }
      });

  }

  getAllTechnician() {
    this.apiservice.getAllTechnician().subscribe(data => {
      if (data) {
        this.technicians = data;
        console.log(data);
        //this.setPage(1);
        // if(this.towers.length>0)
        // {
        //   this.getOfficesByBuildingId(0,this.towers[0].towerId);
        // }
      }
    });
  }
  assignTechnician(technicianId) {
    
    console.log("tecId=====-", technicianId);
    if(!technicianId)
    return;
    let technicianModel=this.technicians.filter(data=>data._id==technicianId);
    this.apiservice.assignTechnicianByReqId(this.requestId, technicianId).subscribe(data => {
      //  if (data.status) {
      // this.technicians = data;
      //console.log(data);
      

      this.notificationModel.notification = "El administrador le asignó el boleto.";
      this.notificationModel.email =technicianModel[0].email;
      this.notificationModel.requestId = this.requestId;
      this.createNotification();
      //this.clearAllFilter();
      this.showTechModal = false;
      alert("El ticket ha sido asignado a este técnico con éxito");
      window.location.reload();
      //this.setPage(1);
      // if(this.towers.length>0)
      // {
      //   this.getOfficesByBuildingId(0,this.towers[0].towerId);
      // }
      //  }
    });
  }
  openTechnicianList(reqId?) {
    console.log("reqId===", reqId);
    //this.requestId = reqId;
    this.showTechModal = true;
  }
  getOfficeDetail() {
    if (this.officeId) {
      this.apiservice.getOfficeById(this.officeId).subscribe(data => {
        if (data && data.status) {
          this.officeDetail = data.office;
          console.log('office', this.officeDetail);
        }
      });
    }
  }
  getSubjects() {
    if (this.officeId)
      this.apiservice.getSubjects().subscribe(data => {
        if (data && data.length > 0) {
          this.subjects = data;
          console.log("office", this.officeDetail);
        }
      });
  }

  getAllTower() {
    this.apiservice.getTowers().subscribe(data => {
      if (data.status != 0) {
        this.towers = new List<Tower>(data).ToArray();
        this.getFloorsByTowerId();
      }
    });
  }
  getFloorsByTowerId() {
    if (!this.selectedTowerId) {
      this.selectedFloorId = "";
      return;
    }

    this.floors =
      this.towers.length > 0
        ? new List<Tower>(this.towers)
          .Where(x => x.towerId.toString() == this.selectedTowerId)
          .Select(z => z.floor)
          .FirstOrDefault()
        : [];
  }
  getLocalStorage() {
    let local = localStorage.getItem("currentUser");
    if (local) {
      this.userModel = JSON.parse(local);
      this.officeId = this.userModel.officeKey;
    }
  }
  showModal="none";
  zoomImage(url)
  {
    this.showModal="block";
    this.largeImageUrl=url;
  }
  close()
  {
    this.showModal="none";
  }
  activate(elem) {
    //deactivate all first
    this.step1Tab = false;
    this.step2Tab = false;
    this.step3Tab = false;
    this.step4Tab = false;
    this.step5Tab = false;

    switch (elem) {
      case "t1": {
        this.step1Tab = true;
        break;
      }
      case "t1.5": {
        this.step2Tab = true;
        break;
      }
      case "t2": {
        this.step2Tab = true;
        break;
      }
      case "t3": {
        this.step3Tab = true;
        break;
      }
      case "t4": {
        this.step4Tab = true;
        break;
      }
      case "t5": {
        this.step5Tab = true;
        break;
      }
      case "t6": {
        this.step5Tab = true;
        break;
      }
    }
  }
}
