import { CommonService } from './../../services/common.service';
import { ApiService } from './../../services/api.service';
import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-technician",
  templateUrl: "./technician.component.html",
  styleUrls: ["./technician.component.css"]
})
export class TechnicianComponent implements OnInit {
  technicians: any=[];

  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];
  isShowLoader: boolean;
  constructor(private apiservice:ApiService,private pagerService:CommonService) {}

  ngOnInit() {
    this.getAllTechnician();
  }
  getAllTechnician() {
    this.isShowLoader=true;
    this.apiservice.getAllTechnician().subscribe(data => {
      if (data) {
        this.technicians = data;
       // console.log(data);
        this.setPage(1);
        this.isShowLoader=false;
        // if(this.towers.length>0)
        // {
        //   this.getOfficesByBuildingId(0,this.towers[0].towerId);
        // }
      }
    });
  }

  deleteTechnician(techId) {
    if (confirm("¿Seguro que quieres borrar?")) {
    this.apiservice.deleteTechniciaById(techId).subscribe(data => {
      if (data) {
        this.getAllTechnician();
       // console.log(data);
      }
    
    });
  }
  }

  setPage(page: number) {
    // get pager object from service
    this.pager = this.pagerService.getPager(this.technicians.length, page);    
    // get current page of items
    this.pagedItems = this.technicians.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }
}
