module.exports = "/* Photo Upload */\r\n.input-file-trigger input{visibility: hidden;position: absolute;}\r\n.custom-file-upload i { font-size: 45px; }\r\nlabel.custom-file-upload img{width: 100%; height: auto; max-height: 100%;}\r\n.input-file-trigger {\r\npadding: 10px 35px;\r\nbackground: #f0713c;\r\ncolor: #fff;\r\nfont-size: 1em;\r\ntransition: all .4s;\r\ncursor: pointer;\r\nmax-width: 200px;\r\n}\r\n.img-cross-btn{\r\nposition: absolute;\r\nright: -7px;\r\nbackground: #fff;\r\nborder: 1px solid #ccc;\r\npadding: 0 4px;\r\nborder-radius: 50%;\r\ntop: -4px;\r\nfont-size: 10px;\r\n}\r\n.profile-img{\r\nheight: 100%;\r\nwidth: 100%;\r\n-o-object-fit: contain;\r\n   object-fit: contain;\r\n}\r\n/* Photo Upload  */\r\n.uploadimg {\r\n    position: relative;\r\n    max-width: 110px;\r\n    border: 1px solid #ccc;\r\n    padding: 2px;\r\n    height: 110px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    flex-direction: column;\r\n}\r\n.uploadimg input{visibility: hidden; position: absolute;}\r\n.uploadimg label{display: flex; height: 100%; width: 100%; align-items: center; justify-content: center;  margin: 0; background: #efefef}\r\n.uploadimg img{width:100%;}\r\n#myImg {\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    transition: 0.3s;\r\n  }\r\n#myImg:hover {opacity: 0.7;}\r\n/* The Modal (background) */\r\n.modal {\r\n    display: none; /* Hidden by default */\r\n    position: fixed; /* Stay in place */\r\n    z-index: 1; /* Sit on top */\r\n    padding-top: 100px; /* Location of the box */\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%; /* Full width */\r\n    height: 100%; /* Full height */\r\n    overflow: auto; /* Enable scroll if needed */\r\n    background-color: rgb(0,0,0); /* Fallback color */\r\n    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */\r\n  }\r\n/* Modal Content (image) */\r\n.modal-content {\r\n    margin: auto;\r\n    display: block;\r\n    width: 80%;\r\n    max-width: 700px;\r\n  }\r\n/* Caption of Modal Image */\r\n#caption {\r\n    margin: auto;\r\n    display: block;\r\n    width: 80%;\r\n    max-width: 700px;\r\n    text-align: center;\r\n    color: #ccc;\r\n    padding: 10px 0;\r\n    height: 150px;\r\n  }\r\n/* Add Animation */\r\n.modal-content, #caption {  \r\n    -webkit-animation-name: zoom;\r\n    -webkit-animation-duration: 0.6s;\r\n    animation-name: zoom;\r\n    animation-duration: 0.6s;\r\n  }\r\n@-webkit-keyframes zoom {\r\n    from {-webkit-transform:scale(0)} \r\n    to {-webkit-transform:scale(1)}\r\n  }\r\n@keyframes zoom {\r\n    from {-webkit-transform:scale(0);transform:scale(0)} \r\n    to {-webkit-transform:scale(1);transform:scale(1)}\r\n  }\r\n/* The Close Button */\r\n.close {\r\n    position: absolute;\r\n    top: 85px;\r\n    right: 35px;\r\n    color: #ffffff !important ;\r\n    font-size: 25px;\r\n    font-weight: bold;\r\n    transition: 0.3s;\r\n    opacity: 1;\r\n  }\r\n.close:hover,\r\n  .close:focus {\r\n    color: #ffffff !important ;\r\n    text-decoration: none;\r\n    cursor: pointer;\r\n  }\r\n/* 100% Image Width on Smaller Screens */\r\n@media only screen and (max-width: 700px){\r\n    .modal-content {\r\n      width: 100%;\r\n    }\r\n  }"