import { ApiService } from "./../../services/api.service";
import { Component, OnInit } from "@angular/core";
import { AuthService } from "src/app/services/auth.service";
import { CommonService } from "src/app/services/common.service";
import { log } from "util";
import { Router } from "@angular/router";

@Component({
  selector: "app-notification",
  templateUrl: "./notification.component.html",
  styleUrls: ["./notification.component.css"]
})
export class NotificationComponent implements OnInit {
  userModel: any;
  notifications: any = [];

  // array of all items to be paged
  private allItems: any[];
  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];
  isShowLoader: boolean;

  constructor(private apiservice: ApiService,private router: Router, private pagerService: CommonService, private api: AuthService) {}

  ngOnInit() {
    this.getLocalStorage();
    this.getAllNotifications();
  }

  getAllNotifications() {
    let email = "admin@buildingmanager.com";
    this.isShowLoader=true;
    this.apiservice.getNotifications().subscribe(data => {
      this.notifications = data;
      this.setPage(1);
      this.isShowLoader=false;
     // console.log("notifications======", data);
    });
  }
  getLocalStorage() {
    let local = localStorage.getItem("currentUser");
   // console.log(local);

    if (local) this.userModel = JSON.parse(local);
    //sessionStorage.setItem("token",res.token);
  }

  updateNotification(notfcId,reqId?){
    if (notfcId) {
      this.apiservice.updateNotification(notfcId).subscribe(data => {
        if(data)
        console.log(data);
        if(this.userModel.level=="7")
        {
          this.router.navigate(['/MaintenanceEdit', reqId]);
          return
        }
        this.router.navigate(['/EditMaintenance', reqId]);
      });
    }
  }
  // updateNotification(notfcId){
  //   if (notfcId) {
  //     this.apiservice.updateNotification(notfcId).subscribe(data => {
  //       if(data)
  //      // this.notifications.splice(index,1);
  //       this.setPage(1);
  //       //  console.log("notifications======", data);
  //     });
  //   }
  // }
  deleteNotification(notfcId,index) {
    //let email = "admin@buildingmanager.com";
    console.log(notfcId);
    if (confirm("¿Seguro que quieres borrar?")) {
      this.apiservice.deleteNotification(notfcId).subscribe(data => {
        if(data)
        this.notifications.splice(index,1);
        this.setPage(1);
        //  console.log("notifications======", data);
      });
    }
  }
  deleteAllNotifications()
  {
    if (confirm("¿Estás seguro de que deseas eliminar todas las notificaciones?")) {
    this.apiservice.deleteNotificationByEmail().subscribe(data => {
      if(data&&data.status!=0)
      {
      this.isShowLoader = false;
      this.notifications = [];
      this.setPage(1);
      this.pagerService.showMessage("Todas las notificaciones se han eliminado correctamente","danger");
      }
      //this.totalNotifications = this.notifications.filter(data => !data.read).length;
    });
    }
 
    
  }
  setPage(page: number) {
    // get pager object from service
    this.pager = this.pagerService.getPager(this.notifications.length, page);

    // get current page of items
    this.pagedItems = this.notifications.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }
}
