import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { CommonService } from 'src/app/services/common.service';
import { NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
import { ExcelService } from './report.services';
import { List } from "linqts";

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {
  reports: any;
  pager: any = {};
  // paged items
  pagedItems: any[];
  report:any;
  fromDate: any;
  toDate: any;
  filterObj={startDate:'',endDate:'',email:'',isDownload:true};
  clients: any=[];
  isShowLoader: boolean;
  //public filterObj:{startDate:'',endDate:'',isDownload:boolean};
  constructor(private apiservice: ApiService,
              private pagerService: CommonService,
              private parserFormatter: NgbDateParserFormatter,
              private excelService: ExcelService) { }

  ngOnInit() {
    this.getReport();
    // this.getUsersByLevel("7");
    this.getClientsByRequest();
  }
  getReport()
  {
    this.isShowLoader=true;
    this.filterObj.isDownload=true;
    // if(this.fromDate && this.toDate)
    // {
    this.filterObj.startDate=this.parserFormatter.format(this.fromDate);
    this.filterObj.endDate=this.parserFormatter.format(this.toDate);
    //}

    this.apiservice.getReports(this.filterObj).subscribe(data => {
      if (data.status!=0) {

        for (let index in data.reports) {
          let workStartDate = '';
          let item = data.reports[index];

          if (item && item.stepId && item.stepId.technician_info && item.stepId.technician_info.date) {
            workStartDate = item.stepId.technician_info.date.split("T")[0]+"T"+ item.stepId.technician_info.time+":00.000Z";
          }
          data.reports[index]['workStartDate'] = workStartDate;
        }
        this.isShowLoader=false;
        this.reports = data.reports;
        this.setPage(1);
      }
    });
  }

  exportAsXLSX():void {
    let fileData = [];
    if(this.reports.length<=0)
    {
      alert("Please Search Reports");
      return;
    }
    for (let index in this.reports) {
      // let workStartDate = '';
      let item = this.reports[index];

      // if (item && item.stepId && item.stepId.technician_info && item.stepId.technician_info.date) {
      //   workStartDate = item.stepId.technician_info.date.split("T")[0] + item.stepId.technician_info.time+":00.000Z";
      // }
      
        fileData.push({Client: item ? (item.officeKey ? item.officeKey.name: '') : '',
                       Serial: item.requestKey,
                       Department: item ? (item.officeKey ? item.officeKey.department: '') : '',
                       Municipality: item ? (item.officeKey ? item.officeKey.municipality: '') : '',
                       Society: item ? (item.officeKey ? item.officeKey.society: '') : '',
                       Username: item ? (item.officeKey ? item.officeKey.applicant_name: '') : '',
                       "User Code": item ? (item.officeKey ? item.officeKey.applicant_code: '') : '',
                       "Assigned Technician": item ? ( item.stepId ?  (item.stepId.technician_info? item.stepId.technician_info.name:''): '') : '',
                       "Request Type": item ? (item.subjectId ? item.subjectId.name: '') : '',
                       "Application Date And Time": item.created_at,
                       "Approval Date": item? (item.stepId? item.stepId.quotationAccepted_at:''):'',
                       "Work Start Date": item.workStartDate,
                       "Work End Date": item ? (item.stepId? item.stepId.techInfo_at:'' ):'',
                       "Billing Date": item? (item.stepId? item.stepId.updated_at4  : '') :'',
                      "Value of Request":item ? ( item.stepId ?  (item.stepId.quote? item.stepId.quote.comment:''): '') : '',
                      "Status Of Request": item.step,
                      "Technician Notes": item.techComment,
                      "Admin Notes": '-',
                      "Rating/Stars": item ? (item.stepId ? item.stepId.star: '') : '',
                      "Comments": item ? (item.stepId ? item.stepId.comment: '') : '',
                      });
    }
    this.excelService.exportAsExcelFile(fileData, 'Excel2');
 }

  // downloadReport()
  // {
  //   //this.filterObj.isDownload=undefined;
  //   this.filterObj.startDate=this.parserFormatter.format(this.fromDate);
  //   this.filterObj.endDate=this.parserFormatter.format(this.toDate);
  //   this.apiservice.downloadReports(this.filterObj).subscribe(data => {
  //       console.log(data);
  //   });
  // }

  getUsersByLevel(level?) {
    this.apiservice.getUsersByLevel(level).subscribe(data => {
      this.clients = data;
    });
  }
  setPage(page: number) {
    // get pager object from service
    this.pager = this.pagerService.getPager(this.reports.length, page);

    // get current page of items
    this.pagedItems =this.reports.length>0?  this.reports.slice(this.pager.startIndex, this.pager.endIndex + 1):[];
  }

  getClientsByRequest() {
    var filterObj = { step: '', statusId: 3 }
    this.apiservice.getAllRequests(filterObj).subscribe(
      data => {
        //console.log("asgdgsadg",data);
        if (data) {
          let clientEmails = new List<any>(data).Select(z => z.email).Distinct().ToArray();
          this.clients = clientEmails;
        }
      },
      error => {
        console.log(error, "==error===");
      }
    );
  }

}
