  groupBy(xs, key) {
    return xs.reduce(function(rv, x) {
      (rv[x[key]] = rv[x[key]] || []).push(x);
      return rv;
    }, {});
  }

  getStarReview() {
    this.five_stars = [];
    this.four_stars = [];
    this.three_stars = [];
    this.two_stars = [];
    this.one_star = [];
    for (let i = 0; i < this.stars.length; i++) {
      //let loading = this.loadingCtrl.create();
      //loading.present();

      this.apiservice.getUserByEmail(this.stars[i].email).subscribe(
        data => {
          //  loading.dismiss();

          this.apiservice.getOfficeById(data.officeKey).subscribe(
            data1 => {
              if (this.stars[i].star >= 4.5) {
                this.five_stars.push(data1[0].name);
              } else if (this.stars[i].star >= 3.5) {
                this.four_stars.push(data1[0].name);
              } else if (this.stars[i].star >= 2.5) {
                this.three_stars.push(data1[0].name);
              } else if (this.stars[i].star > 1.5) {
                this.two_stars.push(data1[0].name);
              } else {
                this.one_star.push(data1[0].name);
              }
            },
            data1 => {}
          );
        },
        data => {
          //loading.dismiss();
        }
      );
    }
  }
