  downloadReports(filterObj?: any) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Disposition': `attachment; filename="Excel2.xlsx"; filename*=utf-8''Excel2.xlsx;`,
        'Content-Type': 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      }),
      responseType: 'arraybuffer' as 'json',

    };

    return this.httpClient.post<any>(`${environment.apiUrl}/request/exportReport?token=` + this.auth_token, filterObj, httpOptions).pipe(
      map(
        Response => this.downLoadFile(Response, "application/ms-excel"),
        error => {
          let msg = error;
        }
      )
    );
  }

  downLoadFile(data: any, type: string) {
    var blob = new Blob([data], { type: type });
    var url = window.URL.createObjectURL(blob);
    var pwa = window.open(url);
    if (!pwa || pwa.closed || typeof pwa.closed == 'undefined') {
      alert('Please disable your Pop-up blocker and try again.');
    }
  }


