import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { Http, Response, Request, RequestMethod } from "@angular/http";
import { map, filter, catchError, mergeMap } from 'rxjs/operators';
//import { API_URL } from 'src/environments/environment';
import { environment } from '../../environments/environment';
// import "rxjs/add/operator/map";
//const API_URL = "http://egcolumbiaapi.mobilytedev.com/api";
//const API_URL = "http://localhost:8082/api";
@Injectable({
  providedIn: 'root'
})

export class AuthService {

  constructor(private http: Http)  { }


  login(email,password): Observable<any> {
    let request = new Request({
      method: RequestMethod.Post,
      url: environment.apiUrl + "/login",
      body: {email,password, fcm_token:localStorage.getItem('fcm_token')}
    });
    // if (this.auth_token) {
    //   request.headers.set("token", `${this.auth_token}`);
    // }
    return this.http.request(request).pipe(map(response => {
      const json = response.json();
      if (json) {
        return json;
      } else {
        console.log("error", json.message);
        return json;
      }
    }));
  }

  logOut(email): Observable<any> {
    let request = new Request({
      method: RequestMethod.Post,
      url: environment.apiUrl + "/logout",
      body: {email,fcm_token:localStorage.getItem('fcm_token')}
    });
    // if (this.auth_token) {
    //   request.headers.set("token", `${this.auth_token}`);
    // }
    return this.http.request(request).pipe(map(response => {
      const json = response.json();
      if (json) {
        return json;
      } else {
        console.log("error", json.message);
        return json;
      }
    }));
  }

  signUp(model): Observable<any> {
    let request = new Request({
      method: RequestMethod.Post,
      url: environment.apiUrl + "/signup",
      body:  model 
    });
    // if (this.auth_token) {
    //   request.headers.set("token", `${this.auth_token}`);
    // }
    return this.http.request(request).pipe(map(response => {
      const data = response.json();
      if (data) {
        return data;
      } else {
        console.log("error", data.message);
        return data;
      }
    },
    error=>{
      const data = error.json();
    }
    
    ));
  }
  

  
}