import { Component, OnInit } from "@angular/core";
import { UserModel } from "src/app/models/user-model";
import { NgForm } from "@angular/forms";
import { Router } from "@angular/router";
import { Services } from "@angular/core/src/view";
import { AuthService } from "src/app/services/auth.service";
import { Office } from "src/app/models/office";
import { ApiService } from "src/app/services/api.service";
import { Tower } from "src/app/models/tower";
import { List } from "linqts";

@Component({
  selector: "app-sign-up",
  templateUrl: "./sign-up.component.html",
  styleUrls: ["./sign-up.component.css"]
})
export class SignUpComponent implements OnInit {
  // constructor() { }
  userModel: UserModel = new UserModel();
  isShowLoader: boolean;
  towers: any = [];
  floors: any = [];
  selectedTowerId: string = "";
  selectedFloorId: string = "";
  officeDetail: Office = new Office();
  isBasicTab: boolean = true;
  isCompanyTab: boolean = false;
  isPersonalTab: boolean = false;
  subjects: any = [];
  selectedSubjectId: string = "";
  profilePhotoUrl: any = "";
  constructor(private apiservice: ApiService, private api: AuthService, private router: Router) {
    //document.body.className = "bg-image-body";
  }

  ngOnInit() {
    this.getAllTower();
    this.isBasicTab = true;
    this.getSubjects();
    this.officeDetail.level = "3";//Client
  }

  getSubjects() {
    this.apiservice.getSubjects().subscribe(data => {
      if (data && data.length > 0) {
        this.subjects = data;
      }
    });
  }

  signUpTechnician() {
    this.isShowLoader = true;
    var userData = new FormData();
    userData.append("subjectId", this.officeDetail.subjectId ? this.officeDetail.subjectId : '');
    userData.append("company", this.officeDetail.company ? this.officeDetail.company : '');
    userData.append("email", this.officeDetail.email ? this.officeDetail.email : '');
    userData.append("first_name", this.officeDetail.first_name ? this.officeDetail.first_name : '');
    userData.append("last_name", this.officeDetail.last_name ? this.officeDetail.last_name : '');
    userData.append("level", this.officeDetail.level ? this.officeDetail.level : '');
    userData.append("otherSubjectValue", this.officeDetail.otherSubjectValue ? this.officeDetail.otherSubjectValue : '');
    userData.append("floorId", this.selectedFloorId ? this.selectedFloorId : '');
    userData.append("buildingId", this.selectedTowerId ? this.selectedTowerId : '');
    userData.append("phone_number", this.officeDetail.phone_number ? this.officeDetail.phone_number : '');
    userData.append("password", this.officeDetail.password ? this.officeDetail.password : '');
    userData.append("idNumber", this.officeDetail.idNumber ? this.officeDetail.idNumber : '');
    userData.append("images", this.officeDetail.profilePhoto ? this.officeDetail.profilePhoto : '');
    userData.append("fcm_token", localStorage.getItem('fcm_token') ? localStorage.getItem('fcm_token') : '');

    this.api.signUp(userData).subscribe(
      data => {
        this.isShowLoader = false;
        if (data.status != 0) {
          localStorage.setItem("token", data.token);
          localStorage.setItem("currentUser", JSON.stringify(data.user));
          this.router.navigateByUrl('/Maintenance');
        } else {
          alert(data.message);
        }
      },
      error => {
        this.isShowLoader = false;
        console.log(error, "==error===");
      }
    );
  }

  signUpClient() {
    this.isShowLoader = true;
    var userData = new FormData();
    userData.append("applicant_code", this.officeDetail.applicant_code ? this.officeDetail.applicant_code : '');
    userData.append("applicant_name", this.officeDetail.applicant_name ? this.officeDetail.applicant_name : '');
    userData.append("area", this.officeDetail.area ? this.officeDetail.area : '');
    userData.append("client_email", this.officeDetail.client_email ? this.officeDetail.client_email : '');
    userData.append("coPay", this.officeDetail.coPay ? this.officeDetail.coPay : '');
    userData.append("company", this.officeDetail.company ? this.officeDetail.company : '');
    userData.append("otherSubjectValue", this.officeDetail.otherSubjectValue ? this.officeDetail.otherSubjectValue : '');

    userData.append("floorId", this.selectedFloorId ? this.selectedFloorId : '');
    userData.append("buildingId", this.selectedTowerId ? this.selectedTowerId : '');
    userData.append("building_no", this.officeDetail.building_no ? this.officeDetail.building_no : '');
    userData.append("floor_no", this.officeDetail.floor_no ? this.officeDetail.floor_no : '');

    userData.append("department", this.officeDetail.department ? this.officeDetail.department : '');
    userData.append("direction", this.officeDetail.direction ? this.officeDetail.direction : '');
    userData.append("email", this.officeDetail.email ? this.officeDetail.email : '');
    userData.append("garages", this.officeDetail.garages ? this.officeDetail.garages : '');
    userData.append("level", this.officeDetail.level ? this.officeDetail.level : '');
    userData.append("municipality", this.officeDetail.municipality ? this.officeDetail.municipality : '');
    userData.append("name", this.officeDetail.name ? this.officeDetail.name : '');
    userData.append("society", this.officeDetail.society ? this.officeDetail.society : '');
    userData.append("password", this.officeDetail.password ? this.officeDetail.password : '');
    userData.append("images", this.officeDetail.profilePhoto ? this.officeDetail.profilePhoto : '');
    userData.append("fcm_token", localStorage.getItem('fcm_token') ? localStorage.getItem('fcm_token') : '');

    this.api.signUp(userData).subscribe(
      data => {
        this.isShowLoader = false;
        if (data.status != 0) {
          localStorage.setItem("token", data.token);
          localStorage.setItem("currentUser", JSON.stringify(data.user));
          this.router.navigateByUrl('/Maintenance');
        } else {
          alert(data.message);
        }
      },
      error => {
        this.isShowLoader = false;
        console.log(error, "==error===");
      }
    );
  }

  signUp(form: NgForm) {
    if (!form.valid) return;

    if (this.officeDetail['termsAndConditions']) {
      this.officeDetail.buildingId = this.selectedTowerId;
      this.officeDetail.floorId = this.selectedFloorId;
      if (this.selectedSubjectId)
        this.officeDetail.subjectId = this.selectedSubjectId;
      if (this.subjects.length > 0 && this.subjects[this.subjects.length - 1]['_id'] != this.selectedSubjectId)
        this.officeDetail.otherSubjectValue = "";

      if (this.officeDetail.level == '3.2')
        this.signUpTechnician();
      else
        this.signUpClient();
    } else {
      alert("Por favor, consulte los términos y condiciones.");
    }
  }

  activate(elem) {
    //deactivate all first
    this.isBasicTab = false;
    this.isCompanyTab = false;
    this.isPersonalTab = false;

    switch (elem) {
      case 't1': { this.isBasicTab = true; break; }
      case 't2': { this.isCompanyTab = true; break; }
      case 't3': { this.isPersonalTab = true; break; }
    }
  }

  getAllTower() {
    this.apiservice.getTowers().subscribe(data => {
      if (data.status != 0) {
        this.towers = new List<Tower>(data).ToArray();
      }
    });
  }

  imageEvent($event) {
    if ($event.target.files) {
      this.officeDetail.profilePhoto = $event.target.files[0];
      this.readUrl($event.target.files[0]);
    }
  }

  readUrl(file: File) {
    if (file) {
      var reader = new FileReader();
      reader.onload = (event: ProgressEvent) => {
        this.profilePhotoUrl = (<FileReader>event.target).result;
        //this.images.push(imgurl);
      };
      reader.readAsDataURL(file);
    }
  }
  removeImage() {
    this.profilePhotoUrl = '';
    this.officeDetail.profilePhoto = '';
  }

  getFloorsByTowerId() {

    if (!this.selectedTowerId || this.selectedTowerId === "4") {
      this.selectedFloorId = "";
      return;
    }

    this.floors =
      this.towers.length > 0
        ? new List<Tower>(this.towers)
          .Where(x => x.towerId.toString() == this.selectedTowerId)
          .Select(z => z.floor)
          .FirstOrDefault()
        : [];
    // console.log(this.floors);

    //this.floors=arr.Where(x => x.towerId == this.selectedTowerId).ToArray();
    //.Select(z=>z.floor).ToArray();
  }
}
