  ngOnInit() {
    this.getLocalStorage();
    this.quoteDate = this.calendar.getToday();
    console.log("quote date", this.quoteDate);
    this.route.params.subscribe(params => {
      this.requestId = params["id"];
      this.getRequestById();
      
    });

    this.getSubjects();
  }

  
  getRequestById() {
    if (this.requestId)
      this.apiservice.getRequestById(this.requestId).subscribe(data => {
        if (data && data.length > 0) {
          this.requestModel = data[0];
          console.log("thhis.request model", this.requestModel);
          let data1:any=this.requestModel.officeKey;
          this.officeDetail=data1;
          if(this.requestModel.techPhoto.length>0)
          {
            this.images=this.requestModel.techPhoto;
          }
          this.getStepByReqId();
        }
      });
  }

  getStepByReqId() {
    if (this.requestId)
      this.apiservice.getStepByReqId(this.requestId).subscribe(data => {
        if (data) {
          this.stepModel = data;
          // this.quoteDate=  new Date(this.stepModel.quote.date);
          this.quoteDate = new NgbDate(new Date(this.stepModel.quote.date).getFullYear(),
                                       new Date(this.stepModel.quote.date).getMonth()+1,
                                       new Date(this.stepModel.quote.date).getDate());
          this.stepModel.quote = data.quote ? data.quote : new Quote();
          this.stepModel.technician_info = data.technician_info ? data.technician_info : new Technician();
          this.stepId = data._id;

          if (this.stepModel && this.stepModel.technician_info && this.stepModel.technician_info.date) {
            this.techDate = new NgbDate(new Date(this.stepModel.technician_info.date).getFullYear(),
                                       new Date(this.stepModel.technician_info.date).getMonth()+1,
                                       new Date(this.stepModel.technician_info.date).getDate());
          }

          this.stepRating=parseInt(this.stepModel.star);
          this.activate("t" + this.stepModel.step);
          this.technicianKey = this.requestModel.technicianKey; 
          this.getUserById();
        }
      });
  }
  getUserById() {
    if (this.technicianKey)
      this.apiservice.getUserById(this.technicianKey).subscribe(data => {
        if (data && data.length > 0) {
          console.log("technician data", data);
          this.technicianModel = data[0];
          this.technicianName=this.technicianModel.first_name+' '+this.technicianModel.last_name;
        }
      });
  }
