  updateRequest(step, statusID) {
    if (this.requestId) {
      let requestData = { step: step, statusId: statusID };

      if (this.reasonToRejectFirstVisit) {
        requestData['reasonToRejectFirstVisit'] = this.reasonToRejectFirstVisit
      }

      if (this.reasonToRejectSecondVisit) {
        requestData['reasonToRejectSecondVisit'] = this.reasonToRejectSecondVisit;
      }

      this.apiservice.updateRequestDetail(this.requestId, requestData).subscribe(data => {
        if (data.status != 0) {
          //this.requestModel = data[0];
          if (this.reasonToRejectFirstVisit || this.reasonToRejectSecondVisit) {
            window.location.href  = '/Maintenance';
          }
        }
      });
    }
  }
  
