  saveTechInfo()
  {

    if(!this.requestModel.techComment)
    {
      alert("Por favor ingrese Comentario");
      return;
    }
    var requestData=new FormData();
    requestData.append("techComment",this.requestModel.techComment);
    if(this.imageFiles&&this.imageFiles.length>0)
     for (let i = 0; i < this.imageFiles.length; i++) {
      requestData.append("images", this.imageFiles[i], this.imageFiles[i]["name"]);
     }

     if (this.requestId) {
      //  let requestData = { techComment: this.requestModel.techComment, techPhoto: [] };
        this.apiservice.updateRequestDetail(this.requestId, requestData).subscribe(data => {
          if (data.status != 0) {
            this.stepModel.techInfo_at = new Date().toJSON();
            if (this.stepId)
            this.apiservice.updateStepDetail(this.stepId, this.stepModel).subscribe(data => {
              if (data.status != 0) {
                this.notificationModel.email = "admin@buildingmanager.com";
                var name = this.userModel.first_name + " " + this.userModel.last_name; 
                this.notificationModel.notification = name+ " finalizó el trabajo.";
                this.notificationModel.requestId = this.requestId;
                this.createNotification();
              }
            });

           // console.log("request", this.requestModel);
          }
        });
      }
  }

 

