  removeImage(index)
  {

    this.images.slice(index,1);
    //this.imageFiles.slice(index,1);
    this.images.splice(index, 1);
    let imgFiles = [];
    Array.prototype.push.apply(imgFiles, this.imageFiles);
    imgFiles.splice(index, 1);
    this.imageFiles = imgFiles;

  }
imageEvent($event) {
  // if ($event.target.files) {
  //   this.requestModel.photos = $event.target.files[0];
  //   this.readUrl($event.target.files[0]);
  // }
  let existImageLength=this.images.length + $event.target.files.length;
  if (existImageLength>3 || (this.images.length > 2 || $event.target.files.length >= 4)) {
    alert("Por favor seleccione un máximo de tres imágenes");
    return;
  }
  
  let picFiles = <Array<File>>$event.target.files;
  for (let index = 0; index < picFiles.length; index++) {
    const imageFile= picFiles[index];
    this.imageFiles.push(imageFile);
    this.readUrl(imageFile);
  }
}
readUrl(file: File) {
  if (file) {
    var reader = new FileReader();
    //this.imageFileName = file.name;
    reader.onload = (event: ProgressEvent) => {
      let imgurl = (<FileReader>event.target).result;
      this.images.push(imgurl);
    };
    reader.readAsDataURL(file);
  }
}


