  getSubjects() {
    this.apiservice.getSubjects().subscribe(data => {
      if (data && data.length > 0) {
        this.subjects = data;
        //console.log("office",this.officeDetail);
      }
    });
  }
  createRequest(form: NgForm) {
    if (!form.valid || !this.userId || !this.officeId) {
      return;
    }
    this.isShowLoader = true;
    var ts = new Date();
    this.requestModel.subjectId = this.selectedSubjectId;
    this.requestModel.step = 1;
    this.requestModel.statusId = "1";
    this.requestModel.updated_at5 = ts.toJSON();
    this.requestModel.userKey = this.userId;
    this.requestModel.created_at = ts.toJSON();

    let newData = new FormData();
    //newData.append("request",JSON.stringify(this.requestModel));
    newData.append("subjectId", this.selectedSubjectId);
    newData.append("step", "1");
    newData.append("statusId", "1");
    newData.append("userKey", this.userId);
    //newData.append("buildingId",JSON.stringify(this.requestModel.buildingId));
    //newData.append("floorId", JSON.stringify(this.requestModel.floorId));
    newData.append("updated_at5", ts.toJSON());
    newData.append("created_at", ts.toJSON());
    newData.append("comment", this.requestModel.comment);
    newData.append("otherSubjectValue", this.requestModel.otherSubjectValue ? this.requestModel.otherSubjectValue : '');
    newData.append("is_urgent", JSON.stringify(this.requestModel.is_urgent));
    newData.append("officeKey", this.requestModel.officeKey);
    //newData.append("technicianKey", this.requestModel.technicianKey?this.requestModel.technicianKey);
    newData.append("statusId", "1");
    newData.append("buildingId", this.requestModel.buildingId);
    newData.append("floorId", this.requestModel.floorId);
    //newData.append("created_by", this.requestModel.);

    if (this.imageFiles && this.imageFiles.length > 0)
      for (let i = 0; i < this.imageFiles.length; i++) {
        newData.append("images", this.imageFiles[i], this.imageFiles[i]["name"]);
      }

    this.apiservice.createRequest(newData).subscribe(data => {
      // console.log(data);
      if (data.status != 0) {
        this.notificationModel.notification = "Usted ha recibido una nueva solicitud";
        this.notificationModel.email = "admin@buildingmanager.com";//"isllanos@eg-colombia.com";
        this.notificationModel.requestId = data.data._id;
        //this.notificationModel.read = true;
        this.createNotification();
        //alert("Su solicitud ha sido enviada exitosamente.");
        this.router.navigateByUrl("/Maintenance");
        this.commonService.showMessage("Su solicitud ha sido enviada exitosamente.");
        this.isShowLoader = false;
        return;
      }
      else{
        this.isShowLoader = false;
        //this.commonService.showMessage("Something went wrong.");
       // alert("Something went wrong.");
      }
      
    });
  }

  createNotification() {
    this.apiservice.createNotification(this.notificationModel).subscribe(data => {
      //console.log(data);
      let msg = data;
         });
  }

  getAllTower() {
    this.apiservice.getTowers().subscribe(data => {
      if (data.status != 0) {
        this.towers = new List<Tower>(data).ToArray();
        this.getFloorsByTowerId();
      }
    });
  }
  getFloorsByTowerId() {
    if (!this.selectedTowerId) {
      this.selectedFloorId = "";
      return;
    }

    this.floors =
      this.towers.length > 0
        ? new List<Tower>(this.towers)
          .Where(x => x.towerId.toString() == this.selectedTowerId)
          .Select(z => z.floor)
          .FirstOrDefault()
        : [];
  }
  getOfficeDetail() {
    if (this.officeId)
      this.apiservice.getOfficeById(this.officeId).subscribe(data => {
        if (data && data.status) {
          this.officeDetail = data.office;
          this.requestModel.buildingId = this.officeDetail.buildingId;
          this.requestModel.floorId = this.officeDetail.floorId;
          this.requestModel.officeKey = this.officeId;
          this.getFloorsByTowerId();
        }
      });
  }
  removeImage(index) {

    this.images.slice(index, 1);
    //this.imageFiles.slice(index,1);
    this.images.splice(index, 1);
    let imgFiles = [];
    Array.prototype.push.apply(imgFiles, this.imageFiles);
    imgFiles.splice(index, 1);
    this.imageFiles = imgFiles;

  }
  imageEvent($event) {
    // if ($event.target.files) {
    //   this.requestModel.photos = $event.target.files[0];
    //   this.readUrl($event.target.files[0]);
    // }
    let existImageLength = this.images.length + $event.target.files.length;
    if (existImageLength > 3 || (this.images.length > 2 || $event.target.files.length >= 4)) {
      alert("Please select maximum three images");
      return;
    }

    let picFiles = <Array<File>>$event.target.files;
    for (let index = 0; index < picFiles.length; index++) {
      const imageFile= picFiles[index];
      this.imageFiles.push(imageFile);
      this.readUrl(imageFile);
    }
  }
  readUrl(file: File) {
    if (file) {
      var reader = new FileReader();
      this.imageFileName = file.name;
      reader.onload = (event: ProgressEvent) => {
        let imgurl = (<FileReader>event.target).result;
        this.images.push(imgurl);
      };

      reader.readAsDataURL(file);
    }
  }

