import { AuthService } from 'src/app/services/auth.service';
import { MessagingService } from './../../services/messaging.service';
import { filter } from 'rxjs/operators';
import { UserModel } from 'src/app/models/user-model';
import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: "app-searchbar",
  templateUrl: "./searchbar.component.html",
  styleUrls: ["./searchbar.component.css"]
})
export class SearchbarComponent implements OnInit {
  userModel: UserModel = new UserModel();
  isShowLoader: boolean;
  notifications: any = [];
  totalNotifications: any = 0;
  message: any;
  constructor(private router: Router, private apiservice: ApiService, private msgservice: MessagingService, private authService: AuthService) { }

  ngOnInit() {
    this.msgservice.currentMessage.subscribe(data => {
      this.message = data;
      // let total=data.data.total;
      if (this.message) {
        this.totalNotifications = parseInt(this.message.data.total)
      //  alert(this.message.data.total);
      }
    })
    this.getLocalStorage();
    this.getAllNotifications();
  }
  getAllNotifications() {
    this.isShowLoader = true;
    let email = "admin@buildingmanager.com";
    this.isShowLoader = true;
    this.apiservice.getNotifications().subscribe(data => {
      this.isShowLoader = false;
      this.notifications = data;
      this.totalNotifications = this.notifications.filter(data => !data.read).length;


      //this.setPage(1);
      this.isShowLoader = false;
      // console.log("notifications======", data);
    });
  }
  updateNotification(notfcId, reqId) {
    if (notfcId) {
      this.apiservice.updateNotification(notfcId).subscribe(data => {
        if (data)
          console.log(data);
        if (window.location.pathname.indexOf(reqId) != -1) {
          window.location.reload();
          return;
        }
        if (this.userModel.level === "7") {
          this.router.navigate(['/MaintenanceEdit', reqId]);
          return
        }
        this.router.navigate(['/EditMaintenance', reqId]);
        // window.location.reload();
      });
    }
  }
    deleteAllNotifications()
    {
      if (confirm("¿Estás seguro de que deseas eliminar todas las notificaciones?")) {
        this.apiservice.deleteNotificationByEmail().subscribe(data => {
          if(data&&data.status!=0)
          {
          this.isShowLoader = false;
          this.notifications = [];
          this.totalNotifications=0;
          }
          //this.totalNotifications = this.notifications.filter(data => !data.read).length;
        });
      }
    //    this.apiservice.deleteNotificationByEmail().subscribe(data => {
    //   this.isShowLoader = false;
    //   //this.notifications = data;
    //   this.totalNotifications = this.notifications.filter(data => !data.read).length;
    // });
      
    }
  logOut() {

    if (confirm("¿Seguro que quieres cerrar sesión ?")) {

      this.authService.logOut(this.userModel.email).toPromise().then(data => {
        if (data && data.status != 0) {
          //localStorage.clear();
          localStorage.setItem("token","");
          localStorage.setItem("currentUser","");
          //this.router.navigateByUrl("/Login");
           this.router.navigate(['/Login']);
         // window.location.reload();
        }
      });
    }
  }
  getLocalStorage() {
    let local = localStorage.getItem("currentUser");
    if (local)
      this.userModel = JSON.parse(local);
  }
}
