import { ApiService } from "./../../services/api.service";
import { Component, OnInit } from "@angular/core";
import { log } from "util";
import { CommonService } from "src/app/services/common.service";

@Component({
  selector: "app-torres",
  templateUrl: "./torres.component.html",
  styleUrls: ["./torres.component.css"]
})
export class TorresComponent implements OnInit {
  towers: any = [];
  selectedIndex: number ;
  offices: any = [];

  private allItems: any[];
  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];
  isShowLoader: boolean;
  constructor(private apiservice: ApiService,private pagerService: CommonService,) {}

  ngOnInit() {
    this.getAllTower();
  }
  setActiveClass(index: number) {
    this.selectedIndex = index;
  }
  getAllTower() {
    this.isShowLoader=true;
    this.apiservice.getTowers().subscribe(data => {
      if (data) {
        this.towers = data;
        this.isShowLoader=false;
        // if(this.towers.length>0)
        // {
        //   this.getOfficesByBuildingId(0,this.towers[0].towerId);
        // }
      }
    });
  }

  
  getOfficesByBuildingId(index, towerId) {
    this.apiservice.getOfficesByTowerId(towerId).subscribe(data => {
      if (data.status != 0) {
        this.offices = data;
        console.log("Officess==",data);
        this.setPage(1);
        this.setActiveClass(index);
      }
    });
  }
  setPage(page: number) {
    // get pager object from service
    this.pager = this.pagerService.getPager(this.offices.length, page);

    // get current page of items
    this.pagedItems = this.offices.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }
}
