import { UserModel } from 'src/app/models/user-model';
import { Component, OnInit } from '@angular/core';
import { Tower } from 'src/app/models/tower';
import { List } from 'linqts';
import { NgForm } from '@angular/forms';
import { ApiService } from 'src/app/services/api.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Office } from 'src/app/models/office';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  towers: any = [];
  floors: any = [];
  selectedTowerId: string = "";
  selectedFloorId: string = "";
  officeDetail: Office = new Office();
  userModel: UserModel = new UserModel();
  userId: string;
  selectedSubjectId: string = "";
  subjects: any = [];
  profilePhotoUrl:any="";
  isShowLoader: Boolean;

  constructor(private apiservice: ApiService,
              private commonService: CommonService, 
              private route: ActivatedRoute, 
              private router: Router) { }

  officeId: any;
  ngOnInit() {

    this.getLocalStorage();
    if (this.userModel.level === "3") {
      this.getAllTower();
      this.getOfficeDetail();
    }
    if (this.userModel.level === "3.2") {
      this.getSubjects();
      this.getUserDetail();
    }
    console.log("user model", this.userModel);
  }

  AddEdit(form: NgForm) {
    if (form.valid) {
      if (this.userModel.level === "3") {
        this.updateClient();
      }
      else if (this.userModel.level === "3.2") {
        this.updateTechnician();
      }
    }
  }

  updateClient() {
    if (!this.officeId) return;
    this.officeDetail.floorId = this.selectedFloorId;
    this.officeDetail.buildingId = this.selectedTowerId;
    this.apiservice.updateOfficeDetail(this.officeId, this.officeDetail).subscribe(data => {
      if (data.status!=0) {
        this.updateClientPhoto();
        //  let url='/EditTorres/'+this.officeId;
        this.router.navigateByUrl("Maintenance");
        // this.officeDetail = data[0];
      }
    });
  }

  updateClientPhoto() {
    if (!this.userId) return;
    var userData = new FormData();
     userData.append("images", this.userModel.profilePhoto ? this.userModel.profilePhoto : '');
    if(typeof(this.userModel.profilePhoto)=="string")
    userData.append("profilePhoto", this.profilePhotoUrl? this.profilePhotoUrl : '');
    this.apiservice.updateProfilePhotoByUId(this.userId, userData).subscribe(data => {
      if (data && data.status != 0) {
        localStorage.setItem("currentUser", JSON.stringify(data.user));
        this.commonService.showMessage("Actualizado exitosamente");
       // alert("Actualizado exitosamente");
      }
    });
  }

  updateTechnician() {
    if (this.subjects.length > 0 && this.subjects[this.subjects.length - 1]['_id'] != this.selectedSubjectId)
      this.userModel.otherSubjectValue = "";
    this.userModel.subjectId = this.selectedSubjectId;

    var userData = new FormData();
    userData.append("subjectId", this.userModel.subjectId ? this.userModel.subjectId : '');
    userData.append("company", this.userModel.company ? this.userModel.company : '');
    userData.append("email", this.userModel.email ? this.userModel.email : '');
    userData.append("first_name", this.userModel.first_name ? this.userModel.first_name : '');
    userData.append("last_name", this.userModel.last_name ? this.userModel.last_name : '');
    userData.append("level", this.userModel.level ? this.userModel.level : '');
    userData.append("idNumber", this.userModel.idNumber ? this.userModel.idNumber : '');
    userData.append("otherSubjectValue", this.userModel.otherSubjectValue ? this.userModel.otherSubjectValue : '');
    userData.append("phone_number", this.userModel.phone_number ? this.userModel.phone_number : '');
    userData.append("images", this.userModel.profilePhoto ? this.userModel.profilePhoto : '');
    if(typeof(this.userModel.profilePhoto)=="string")
    userData.append("profilePhoto", this.profilePhotoUrl? this.profilePhotoUrl : '');
    this.isShowLoader = true;
    this.apiservice.updateUserById(this.userId, userData).subscribe(data => {
      if (data && data.status != 0) {
        this.isShowLoader=false;
        localStorage.setItem("currentUser", JSON.stringify(data.user));
        //alert("Actualizado exitosamente");
      }
    })
  }

  getOfficeDetail() {
    if (this.officeId)
      this.apiservice.getOfficeById(this.officeId).subscribe(data => {
        if (data && data.status) {
          this.officeDetail = data.office;
          this.selectedTowerId = this.officeDetail.buildingId;
          this.selectedFloorId = this.officeDetail.floorId;
          this.profilePhotoUrl=this.userModel.profilePhoto;
          //this.officeDetail.profilePhoto;
          this.getFloorsByTowerId();
        }
      });
  }
  getUserDetail() {
    if (this.userId)
      this.apiservice.getUserById(this.userId).subscribe(data => {
        if (data) {
          this.userModel = data[0];
          this.selectedSubjectId = data[0].subjectId._id;
          this.profilePhotoUrl=this.userModel.profilePhoto;
        }
      });
  }
  getAllTower() {

    this.apiservice.getTowers().subscribe(data => {
      if (data.status != 0) {
        this.towers = new List<Tower>(data).ToArray();
        this.getFloorsByTowerId();
      }
    });
  }
  getFloorsByTowerId() {
    if (!this.selectedTowerId) {
      this.selectedFloorId = "";
      return;
    }

    this.floors =
      this.towers.length > 0
        ? new List<Tower>(this.towers)
          .Where(x => x.towerId.toString() == this.selectedTowerId)
          .Select(z => z.floor)
          .FirstOrDefault()
        : [];


  }
  getSubjects() {
    this.apiservice.getSubjects().subscribe(data => {
      if (data && data.length > 0) {
        this.subjects = data;
        //console.log("office",this.officeDetail);
      }
    });
  }

  imageEvent($event) {
    if ($event.target.files) {
      this.userModel.profilePhoto = $event.target.files[0];
      this.readUrl($event.target.files[0]);
    }
  }
  readUrl(file: File) {
    if (file) {
      var reader = new FileReader();
      reader.onload = (event: ProgressEvent) => {
        this.profilePhotoUrl = (<FileReader>event.target).result;
      };
      reader.readAsDataURL(file);
    }
  }
  removeImage() {
    this.profilePhotoUrl = '';
    this.userModel.profilePhoto = '';
  }
  getLocalStorage() {
    let local = localStorage.getItem("currentUser");
    if (local) {
      this.userModel = JSON.parse(local);
      this.officeId = this.userModel.officeKey;
      this.userId = this.userModel._id;
    }
  }

  showModal="none";
  open()
  {
    this.showModal="block";

  }
  close()
  {
    this.showModal="none";
  }

}
