import { Tower } from "./../../models/tower";
import { ApiService } from "./../../services/api.service";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { List } from "linqts";
import { log } from "util";
import { Office } from "src/app/models/office";
import { NgForm } from "@angular/forms";

@Component({
  selector: "app-edit-torres",
  templateUrl: "./edit-torres.component.html",
  styleUrls: ["./edit-torres.component.css"]
})
export class EditTorresComponent implements OnInit {
  towers: any = [];
  floors: any = [];
  selectedTowerId: string = "";
  selectedFloorId: string = "";
  officeDetail: Office = new Office();

  constructor(private apiservice: ApiService, private route: ActivatedRoute, private router: Router) {}

  officeId: string;
  ngOnInit() {
    this.getAllTower();
    this.route.params.subscribe(params => {
      this.officeId = params["id"];
      //console.log(this.officeId);
    
    });
  }
  AddEdit(form:NgForm)
  {
    if(form.valid)
    {
      this.officeDetail.buildingId=this.selectedTowerId;
      this.officeDetail.floorId=this.selectedFloorId;
      this.apiservice.updateOfficeDetail(this.officeId,this.officeDetail).subscribe(data => {
      if (data) {
        let url='/EditTorres/'+this.officeId;
        this.router.navigateByUrl("Tower");
       // this.officeDetail = data[0];
      }
    });
      
    }
  }
  getOfficeDetail() {
    if(this.officeId)
    this.apiservice.getOfficeById(this.officeId).subscribe(data => {
      if (data && data.status) {
        this.officeDetail = data.office;
        this.selectedTowerId=this.officeDetail.buildingId;
        this.selectedFloorId=this.officeDetail.floorId;
        this.getFloorsByTowerId();
      }
    });
  }
  getAllTower() {
    
    this.apiservice.getTowers().subscribe(data => {
      if (data.status != 0) {
        this.towers = new List<Tower>(data).ToArray();
        this.getOfficeDetail();
        // this.getFloorsByTowerId();
        // if(this.towers.length>0)
        // {
        //   this.getOfficesByBuildingId(0,this.towers[0].towerId);
        // }
      }
    });
  }
  getFloorsByTowerId() {
    if(!this.selectedTowerId)
    {
      this.selectedFloorId="";
      return;
    }

    this.floors =
      this.towers.length > 0
        ? new List<Tower>(this.towers)
            .Where(x => x.towerId.toString() == this.selectedTowerId)
            .Select(z => z.floor)
            .FirstOrDefault()
        : [];
  //  console.log(this.floors);

    //this.floors=arr.Where(x => x.towerId == this.selectedTowerId).ToArray();
    //.Select(z=>z.floor).ToArray();
  }
}
